<template>
  <div id="evaluDetails-wrapper">
    <Table ref="table2" border stripe :loading="loading2" :columns="columns2" :data="data2"  size="small" highlight-row :max-height="tableHeight2"></Table>
    <div class="page_box" id="pageBox">
      <Page :total="total2" :page-size="pageSize2" show-elevator @on-change="pageChange2" size="small" show-total/>
    </div>
    <Button type="primary" size="small" icon="ios-arrow-back" class="sign-btn" @click="$emit('signOut',false)">返回</Button>
  </div>
</template>

<script>
export default {
  name: 'evaluationDetails',
  data () {
    return {
      /* 加载 */
      loading2: false,
      /* 表格 */
      columns2: [
        {
          title: '序号',
          align: 'center',
          width: 100,
          render: (h, params) => {
            return h('span', params.index + (this.pageNum2 - 1) * this.pageSize2 + 1)
          }
        },
        {
          title: '红线斑块名称',
          align: 'center',
          key: 'hxcode'
        },
        {
          title: '人类活动面积',
          align: 'center',
          key: 'insectarea'
        },
        {
          title: '总面积',
          align: 'center',
          key: 'hxarea'
        },
        /* {
          title: '系数id',
          align: 'center',
          key: 'type'
        },
        {
          title: '系数',
          align: 'center',
          key: 'typeid'
        }, */
        {
          title: '干扰评价',
          align: 'center',
          key: 'typenum'
        }
      ],
      /* 表格动态高度 */
      tableHeight2: window.innerHeight - 290,
      /* 每页条数 */
      pageSize2: Math.floor((window.innerHeight - 290) / 40) - 1,
      /* 页码 */
      pageNum2: 1,
      /* 页面总条数 */
      total2: 0,
      /* 获取data */
      alldata: [],
    }
  },
  props: {
    data: {
      type: Array,
      default: () => {
        return []
      }
    },
    numformItem: {}
  },
  computed: {
    data2: function () {
      let arr = []
      for (let item of this.data) {
        let obj = JSON.parse(JSON.stringify(item.attributes))
        obj.typeid = this.numformItem[obj.type]
        obj.typenum = (this.numformItem[obj.type] * obj.insectarea) / obj.hxarea
        arr.push(obj)
      }
      let dataArr = []
      for (let item of arr) {
        let juice = false
        for (let ite of dataArr) {
          if (ite.hxcode === item.hxcode) {
            juice = true
          }
        }
        if (!juice) {
          dataArr.push(item)
        } else {
          for (let i in dataArr) {
            if (dataArr[i].hxcode === item.hxcode) {
              dataArr[i].insectarea = dataArr[i].insectarea + item.insectarea
              dataArr[i].typenum = dataArr[i].typenum * 1 + item.typenum * 1
              dataArr[i].typenum = (dataArr[i].typenum * 1).toFixed(2)
            }
          }
        }
      }
      console.log(arr)
      return dataArr
    }
  },
  methods: {
    /* 分页跳转 */
    pageChange2 (pageNum) {
      this.pageNum2 = pageNum
    }
  }
}
</script>

<style lang="scss" rel="stylesheet/scss" type="text/scss" scoped>
  #evaluDetails-wrapper {
    position: relative;
    width: 100%;
    height: 100%;
    .page_box{
      width: 100%;
      padding: 15px 0 0;
      text-align: right
    }
    .sign-btn {
      position: absolute;
      right: 20px;
      bottom: 10px;
    }
  }
</style>
