<template>
  <div id="mapbox" v-on:keyup.122 = "fullClose" v-on:keyup.27 = "fullClose">
    <div id="viewDiv"></div>
    <!-- 地图的工具框 -->
    <ButtonGroup id="toolbar" vertical style="display: flex;flex-direction: column">
      <Tooltip content="距离测量" placement="right">
        <Button :type="type=='distance'?'primary':'default'" @click="clickToolbar('distance')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/length.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
      <Tooltip content="测量面积" placement="right">
        <Button :type="type=='area'?'primary':'default'" @click="clickToolbar('area')" style="padding:5px 5px 0px">
          <img src="@/assets/map_images/area.png" alt="" style="width: 20px;height: 20px;">
        </Button>
      </Tooltip>
    </ButtonGroup>
  </div>
</template>

<script>
import arcgisPackage from '../../../utils/arcgisTools'
import config from '../../../setting/mapConfig'
import { tdtlayer, tdtyNoteslayer, tdtclayer, tdtcNoteslayer, redLine, shengjie, shijie, xianjie } from '@/utils/layers'
import Basemapthumbnail from '../../../assets/map_images/Basemapthumbnail.png'
import Basemapthumbnail1 from '../../../assets/map_images/Basemapthumbnail1.png'

export default {
  name: 'OnePiece',
  data () {
    return {
      map: null, // 地图实例
      view: null, // 地图视图实例
      type: '',
      addLayer: {}, // 增加的斑块图层
      subLayer: {} // 减少的斑块图层
    }
  },
  props: {
    detailData: {},
    boxstate: {},
    numformItem: {}
  },
  methods: {
    /**
     * @Description: 初始化“一张图”数据中心的地图
     * @author 张淼
     * @date 2019/4/16
    */
    async createMap () {
      // 导入需要的arcgis模块
      let MapView = await arcgisPackage.MapView
      let Map = await arcgisPackage.Map
      let Basemap = await arcgisPackage.Basemap
      let BasemapToggle = await arcgisPackage.BasemapToggle
      let Fullscreen = await arcgisPackage.Fullscreen
      let GraphicsLayer = await arcgisPackage.GraphicsLayer

      // 天地图影像地图
      let tdtylayer = await tdtlayer().then(res => { return res })
      // 天地图矢量地图
      let tdtylayer1 = await tdtclayer().then(res => { return res })
      // 天地图的矢量标注图层
      let tdtylayer2 = await tdtcNoteslayer().then(res => { return res })
      // 天地图影像标注图层
      let tdtylayer3 = await tdtyNoteslayer().then(res => { return res })

      let customBasemap = new Basemap({
        baseLayers: [tdtylayer1, tdtylayer2],
        title: '矢量地图',
        id: 'myBasemap',
        thumbnailUrl: Basemapthumbnail
      })
      let customBasemap1 = new Basemap({
        baseLayers: [tdtylayer, tdtylayer3],
        title: '影像地图',
        id: 'myBasemap1',
        thumbnailUrl: Basemapthumbnail1
      })

      this.map = new Map({
        basemap: customBasemap
      })
      this.view = new MapView({
        map: this.map,
        container: 'viewDiv',
        center: config.centerPoint,
        zoom: 7
      })
      this.addLayer = new GraphicsLayer({
        graphics: []
      })
      this.subLayer = new GraphicsLayer({
        graphics: []
      })
      this.map.add(this.addLayer)
      this.map.add(this.subLayer)
      // 添加工具框
      this.view.ui.add(document.getElementById('toolbar'), 'top-left')
      // 添加全屏
      this.fullscreen = new Fullscreen({
        view: this.view
      })
      this.view.ui.add(this.fullscreen, 'top-left')
      // 增加地图切换插件
      let basemapToggle = new BasemapToggle({
        view: this.view,
        nextBasemap: customBasemap1
      })
      this.view.ui.padding = { top: 20, left: 20, right: 55, bottom: 20 }
      this.view.ui.add(basemapToggle, 'top-right')
      this.addhxLayer()
    },
    async addhxLayer () {
      let SimpleRenderer = await arcgisPackage.SimpleRenderer
      let FeatureLayer = await arcgisPackage.FeatureLayer
      let Legend = await arcgisPackage.Legend
      let Field = await arcgisPackage.Field
      // 计算干扰评价
      let arr = []
      for (let item of this.detailData) {
        let obj = JSON.parse(JSON.stringify(item.attributes))
        obj.typeid = this.numformItem[obj.type]
        obj.typenum = (this.numformItem[obj.type] * obj.insectarea) / obj.hxarea
        arr.push(obj)
      }
      let dataArr = []
      for (let item of arr) {
        let juice = false
        for (let ite of dataArr) {
          if (ite.hxcode === item.hxcode) {
            juice = true
          }
        }
        if (!juice) {
          dataArr.push(item)
        } else {
          for (let i in dataArr) {
            if (dataArr[i].hxcode === item.hxcode) {
              dataArr[i].insectarea = dataArr[i].insectarea + item.insectarea
              dataArr[i].typenum = dataArr[i].typenum * 1 + item.typenum * 1
              dataArr[i].typenum = dataArr[i].typenum.toFixed(2)
            }
          }
        }
      }
      for (let i in this.detailData) {
        for (let item of dataArr) {
          if (this.detailData[i].attributes.hxcode === item.hxcode) {
            this.detailData[i].attributes.typenum = item.typenum * 1
          }
        }
        this.detailData[i].attributes.int1 = 1
        this.detailData[i].geometry.type = 'polygon'
      }
      console.log(this.detailData)
      // 从config文件中获取图层字段
      let fields = [
        new Field({
          name: 'ObjectID',
          alias: 'ObjectID',
          type: 'oid'
        }),
        new Field({
          name: 'int1',
          alias: 'int1',
          type: 'integer'
        }),
        new Field({
          name: 'typenum',
          alias: 'typenum',
          type: 'double'
        })
      ]
      let symbol = {
        type: 'simple-fill', // 自动生成新的simpleFillSymbol
        color: [0, 18, 255, 0.4],
        outline: {
          // 自动生成新的SimpleLineSymbol
          color: [0, 54, 255],
          width: 2
        }
      }
      const less35 = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [60, 164, 250],
        style: 'solid',
        outline: {
          width: 0.2,
          color: [255, 255, 255, 0.5]
        }
      }
      const less50 = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [247, 240, 144],
        style: 'solid',
        outline: {
          width: 0.2,
          color: [255, 255, 255, 0.5]
        }
      }
      const more50 = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [255, 98, 0],
        style: 'solid',
        outline: {
          width: 0.2,
          color: [255, 255, 255, 0.5]
        }
      }
      const more75 = {
        type: 'simple-fill', // autocasts as new SimpleFillSymbol()
        color: [255, 0, 0],
        style: 'solid',
        outline: {
          width: 0.2,
          color: [255, 255, 255, 0.5]
        }
      }
      let orchardRenderer = new SimpleRenderer({ symbol: symbol })
      orchardRenderer = {
        type: 'class-breaks', // autocasts as new ClassBreaksRenderer()
        classBreakInfos: [
          {
            minValue: 0.2,
            maxValue: 0.4,
            symbol: less35,
            label: '0.2 - 0.4' // label for symbol in legend
          },
          {
            minValue: 0.4,
            maxValue: 0.6,
            symbol: less50,
            label: '0.4 - 0.6' // label for symbol in legend
          },
          {
            minValue: 0.6,
            maxValue: 0.8,
            symbol: more50,
            label: '0.6 - 0.8' // label for symbol in legend
          },
          {
            minValue: 0.75,
            maxValue: 10.0,
            symbol: more75,
            label: '> 0.8' // label for symbol in legend
          }
        ],
        field: 'typenum', // total number of adults (25+) with a college degree
        normalizationField: 'int1', // total number of adults 25+
        defaultSymbol: {
          type: 'simple-fill', // autocasts as new SimpleFillSymbol()
          color: [115, 178, 115],
          style: 'solid',
          outline: {
            width: 0.2,
            color: [255, 255, 255, 0.5]
          }
        },
        defaultLabel: '0 - 0.2' // legend label for features that don't match a class break
      }
      // 底层数据
      let orchardLayer = new FeatureLayer({
        layerId: 'orchardLayer',
        id: 'orchardLayer',
        title: '业务数据',
        objectIdField: 'ObjectID',
        source: this.detailData,
        renderer: orchardRenderer,
        fields: fields
      })
      // 将数据图层添加到地图中
      this.map.addMany([orchardLayer])
      const legend = new Legend({
        view: this.view
      })
      this.view.ui.add(legend, 'bottom-left')
    },
    /**
     * @Description:测量工具点击事件
     * @author 张淼
     * @date 2019/4/11
     */
    async clickToolbar (type) {
      if (this.type === type) {
        type = null
      }
      this.type = type
      if (this.activeWidget) {
        this.view.ui.remove(this.activeWidget)
        this.activeWidget.destroy()
        this.activeWidget = null
      }
      switch (type) {
        case 'distance':
          let DistanceMeasurement2D = await arcgisPackage.DistanceMeasurement2D
          this.activeWidget = new DistanceMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        case 'area':
          let AreaMeasurement2D = await arcgisPackage.AreaMeasurement2D
          this.activeWidget = new AreaMeasurement2D({
            view: this.view
          })
          this.activeWidget.viewModel.newMeasurement()
          this.view.ui.add(this.activeWidget, 'top-right')
          break
        default:
          break
      }
    }
  },
  async mounted () {
    // 初始化地图
    await this.createMap()
  }
}
</script>

<style lang="scss" scoped>
#mapbox{
  height: 100%;
  display: flex;
  position: relative;
}
#layersControl{
  .layersControl_box{
    padding:3px;
    background: #fff;
    border: 1px solid #d1d1d1;
    .fraggableBox{
      padding: 0;
      .fraggableLi{
        min-width: 100px;
        padding: 5px 8px;
        border-top: 1px solid #d1d1d1;
      }
    }
  }
}
#viewDiv{
  height: 100%;
  flex-grow: 2;
}
#maptab{
  width: 300px;
  .firstmenu{
    >li{
      margin-bottom: 6px;
      text-align: left;
      >ul{
        >li{
          padding-left: 20px;
        }
      }
      >p{
        line-height: 40px;
        text-align: left;
        font-size: 16px;
        background-color: rgb(42,177,245);
        color: white;
        padding-left: 10px;
        border-radius: 4px;
        cursor: pointer;
        i{
          font-size: 16px;
          line-height: 40px;
          margin-right: 10px;
        }
      }
      .secondmenu{
        label{
          line-height: 30px;
          font-size: 14px;
        }
      }
    }
  }
}
#basemapToggle{
  border: 5px #000 solid;
}
</style>
<style scoped>
#maptab >>> .ivu-tabs-content{
  height: calc(100% - 40px);
  margin-top: -16px;
}
#maptab >>> .ivu-tabs-content > .ivu-tabs-tabpane{
  padding: 16px;
  border: 1px solid #dcdee2;
  border-radius: 0 0 4px 4px;
  border-top: none;
}
#maptab >>> .ivu-tabs-nav{
  display: flex;
  float: none;
}
#maptab >>> .ivu-tabs-nav .ivu-tabs-tab{
  flex-grow: 2;
  margin-right: 0;
}
  .sliderBox >>> .ivu-slider-wrap{
    margin: 0 0 3px
  }
</style>
<style>
  .esri-legend__layer-caption{
    display: none;
  }
  .esri-ui-corner .esri-component.esri-widget--panel{
    width: 200px;
  }
</style>
