<template>
  <div id="evaluation-wrapper">
    <div class="query-box">
      <!-- 搜索固定标题行 -->
      <span class="search-title">查询条件</span>
      <div class="search-form">
        <Button type="success" size="small" @click="activitiesModal = true" v-model="activitiesValue"  v-html="activitiesContent" style="margin-right: 10px"></Button>
        <!--<i-select v-model.trim="city" style="width: 120px;margin: 0 30px 0 20px" placeholder="请选择市" @on-change="searchCity" size="small">
          <i-option v-for="item in cityList" :value="item.value">{{ item.label }}</i-option>
        </i-select>-->
        <Button type="primary" size="small" icon="ios-search" @click="numModal = true">评分系数</Button>
      </div>
    </div>
    <div class="content_box">
      <Map id="map" ref="maps" :detailData="detailData" :numformItem="numformItem"></Map>
      <!--<Map id="map" ref="maps" :boxstate="boxstate" @layerConClick="layerConClick"></Map>-->
      <div class="table-wrapper">
        <p>评估分值：{{grape}}</p>
        <div style="height: calc(100% - 66px);overflow: auto;">
          <Table :data="alltablemetriesArr" :columns="columns2" :disabled-hover="true" border :span-method="handleSpan"></Table>
        </div>
        <p style="text-align: center;">
          <Button type="primary" style="margin-right: 10px" @click="addAudit">重新计算</Button>
          <Button type="primary" @click="addActive = true">查看详情</Button>
        </p>
        <!-- 查看干扰评价详情-->
        <div class="seeDetails" :class="{addActive:addActive}">
          <evaluationDetails ref="evaluationDetails" @signOut="signOut" :data="detailData" :numformItem="numformItem"></evaluationDetails>
        </div>
      </div>
    </div>
    <!-- 人类活动列表-->
    <Modal
      v-model="activitiesModal"
      title="人类活动列表"
      @on-cancel="handleCancel"
    width="1000">
      <div>
        <Table border stripe :loading="aLoading" :columns="aColumns1"
               :data="aData1" highlight-row :height="aTableHeight"
               ref="activitiesMultipleTable"
               @on-selection-change="handleSelectionChange"></Table>
        <div class="page_box" style="width: 100%;padding: 15px;text-align: right">
          <Page :total="aTotal" :page-size="aPageSize" :current.sync="aPageNum" @on-change="aPageChange" show-total/>
        </div>
      </div>
      <div slot="footer">
        <i-button type="text" @click="handleCancel" class="mr10">取消</i-button>
        <Button type="primary" @click="selectActivities">确定</Button>
      </div>
    </Modal>
    <!-- 评分系统选择 -->
    <Modal
      v-model="numModal"
      title="评分系数"
      width="1000"
    >
      <div style="height: 600px;overflow: auto;">
        <p style="padding: 10px 0 10px 20px;border-bottom: 1px solid #eee;margin-bottom: 20px">
          <span>权重模板</span>
          <i-select v-model.trim="numact" style="width: 150px;margin: 0 30px 0 20px" placeholder="请选评分系数模型" @on-change="searchCity" size="small">
            <i-option v-for="item in numListData" :value="item">{{ item }}</i-option>
          </i-select>
        </p>
        <Form v-model="numformItem" :label-width="140" inline>
          <FormItem v-for="(item, index) in numData" :key="index" :label="item.data">
            <Input v-model="numformItem[item.imageConfigId]" :placeholder="item.data"></Input>
          </FormItem>
        </Form>
      </div>
      <div slot="footer">
        <i-button type="text" @click="numModal = false" class="mr10">取消</i-button>
        <Button type="primary">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { getRemoteList, getAudit, getActiveLists, getNumberNames, getNumberByName, oldNumber, addAudit } from '@/api/remoteSensing'
import { getMenu } from '@/api/acquisitionTable'
import evaluationDetails from './evaluationDetails'
import Map from './map'
export default {
  name: 'evaluationList',
  components: {
    // 查看详情
    evaluationDetails,
    Map
  },
  data () {
    return {
      city: '',
      cityList: [],
      /* 加载 */
      loading1: false,
      /* 存储新增的斑块 */
      addGeometriesArr: [],
      /* 存储减少的斑块 */
      subGeometriesArr: [],
      /* 全部斑块数组 */
      allGeometriesArr: [],
      /* 全部斑块数组 */
      alltablemetriesArr: [],
      /* 详情中斑块表格 */
      columns2: [
        {
          title: '一类标准',
          key: 'name1',
          align: 'center',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '二类标准',
          key: 'name2',
          align: 'center',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '三类标准',
          key: 'name',
          align: 'center',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '面积',
          key: 'num1',
          align: 'center',
          ellipsis: true,
          tooltip: true
        },
        {
          title: '变化量',
          key: 'num2',
          align: 'center',
          ellipsis: true,
          tooltip: true,
          render: (h, params) => {
            return h('span', params.row.num2 === null ? '-' : params.row.num2)
          }
        }
      ],
      data1: [],
      /* 表格动态高度 */
      tableHeight1: window.innerHeight - 290,
      /* 查看详情弹框状态 */
      addActive: false,
      /*
      * 人类活动
      * */
      // 选中人类活动id
      activitiesValue: '',
      // 选中人类活动name
      activitiesContent: '请选择人类活动',
      // 人类活动列表弹窗
      activitiesModal: false,
      // 评分系数
      numModal: false,
      // 评分系数列表
      numListData: false,
      // 评分系数激活的系数名称
      numact: '',
      // 评分模型
      numData: '',
      // 选中的评分系数数据
      numformItem: {},
      // 详情组件数据
      detailData: [],
      /* 表格高度 */
      aTableHeight: Math.floor((window.innerHeight - 300 - 70) / 48) * 48 + 40,
      /* 表格数据 */
      aColumns1: [
        {
          type: 'selection',
          width: 60,
          align: 'center'
        },
        {
          title: '序号',
          key: 'index',
          width: 100,
          align: 'center',
          render: (h, params) => {
            return h('span', params.index + (this.aPageNum - 1) * this.aPageSize + 1)
          }
        },
        {
          title: '任务名称',
          align: 'center',
          minWidth: 200,
          key: 'name'
        },
        {
          title: '说明',
          align: 'center',
          minWidth: 250,
          key: 'remark'
        },
        {
          title: '底图服务',
          align: 'center',
          width: 300,
          key: 'url'
        }
      ],
      aData1: [],
      /* 总条数 */
      aTotal: 0,
      aPageNum: 1,
      aPageSize: Math.floor((Math.floor((window.innerHeight - 300 - 40) / 48) * 48 + 40) / 48),
      /* 加载 */
      aLoading: false,
      // 人类活动多选相关参数
      selectActiveRow: [],
      multipleActiveSelection: [],
      // 农业用地
      farmArr: [],
      // 建设用地
      ConArr: [],
      // 农业用地
      otherArr: [],
      // 评估分数
      grape: 0,
      // 人类活动选中的id
      actListId: ''
    }
  },
  watch: {
    // 人类活动多选
    multipleActiveSelection (data) { // 存储选中的row
      this.selectActiveRow = []
      if (data.length > 0) {
        data.forEach((item, index) => {
          this.selectActiveRow.push(item.id)
        })
      }
    },
    taskIdArr (val) {
      this.taskParamsArr = val
    },
    numact (val) {
      this.getNumberByName(this.numact)
    }
  },
  created () {},
  mounted () {
    /* axios: 获取省市县菜单 */
    this.getCodeMenu()
    /* 获取详情信息 */
    this.getTableList()
    /* 获取人类活动树形列表 */
    // this.ActiveLists()
    /* 评分系数-批次名列表 */
    this.getNumberNames()
  },
  methods: {
    searchCity (val) {
      console.log(val)
    },
    /* axios: 获取省市县菜单 */
    getCodeMenu () {
      getMenu({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          this.cityList = []
          res.data.data[0].children.forEach(element => {
            this.cityList.push({
              label: element.label,
              value: element.comCode
            })
          })
          this.cityList.unshift({
            label: '全部',
            value: ''
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取详情信息 */
    getMapTableList (id) {
      getAudit({
        data: {
          id: id.toString()
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.alltablemetriesArr = res.data.data.imageConfig === null ? [] : res.data.data.imageConfig
          if (res.data.data.out === '') {
            this.detailData = []
          } else {
            this.detailData = JSON.parse(res.data.data.out).value.features
          }
          this.grape = res.data.data.sum.toFixed(2)
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取对比详情失败', desc: res.data.msg })
        }
      })
    },
    /* 从新计算详情信息 */
    addAudit () {
      this.$Spin.show()
      addAudit({
        data: {
          json: this.numformItem,
          id: this.actListId.toString()
        }
      }).then(res => {
        this.$Spin.hide()
        if (res.data.code === '0000') {
          this.$Notice.success({ title: '重新计算成功' })
          this.getMapTableList(this.actListId.toString())
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '重新计算失败', desc: res.data.msg })
        }
      })
    },
    /* 评分系数-批次名列表 */
    getNumberNames () {
      getNumberNames({
        data: {}
      }).then(res => {
        if (res.data.code === '0000') {
          this.numListData = res.data.data
          this.numact = this.numListData[0]
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取失败', desc: res.data.msg })
        }
      })
    },
    /* 获取详情信息 */
    getNumberByName (name) {
      getNumberByName({
        data: {
          name: name
        }
      }).then(res => {
        if (res.data.code === '0000') {
          this.numData = res.data.data
          for (let item of this.numData) {
            this.numformItem[item.imageConfigId] = item.number
          }
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取失败', desc: res.data.msg })
        }
      })
    },
    /* 获取默认系数 */
    oldNumber (id) {
      oldNumber({
        data: {
          id: id.toString()
        }
      }).then(res => {
        if (res.data.code === '0000') {
          if (res.data.data.sign !== '0') {
            this.getMapTableList(id)
          } else {
            this.numData = res.data.data.list
            for (let item of this.numData) {
              this.numformItem[item.id] = item.num
            }
          }
        } else {
          /* 错误信息弹框 */
          this.$Notice.error({ title: '获取失败', desc: res.data.msg })
        }
      })
    },
    /* axios: 获取人类活动列表数据 */
    getTableList () {
      this.aLoading = true
      getRemoteList({
        data: {
          pageSize: this.aPageSize,
          pageNum: this.aPageNum
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.aData1 = res.data.data.rows
          if (this.aData1.length > 0) {
            this.aData1[0]._checked = true
          }
          this.aTotal = res.data.data.total
          this.aLoading = false
          if (res.data.data.rows.length > 0) {
            this.actListId = res.data.data.rows[0].id
            this.getMapTableList(res.data.data.rows[0].id)
            this.oldNumber(res.data.data.rows[0].id)
          }
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 合并单元格 */
    handleSpan ({ row, column, rowIndex, columnIndex }) {
      let arr = ['name1', 'name2']
      for (let item of arr) {
        if (column.key === item) {
          if (rowIndex !== 0) {
            if (this.alltablemetriesArr[rowIndex - 1][item] === row[item]) {
              return [0, 0]
            } else {
              let len = 0
              for (let i in this.alltablemetriesArr) {
                if (this.alltablemetriesArr[i][item] === row[item]) {
                  len = len + 1
                }
              }
              return [len, 1]
            }
          } else {
            let len1 = 0
            for (let i in this.alltablemetriesArr) {
              if (this.alltablemetriesArr[i][item] === row[item]) {
                len1 = len1 + 1
              }
            }
            return [len1, 1]
          }
        }
      }
    },
    /* 人类活动列表分页跳转 */
    aPageChange (pageNum) {
      this.aPageNum = pageNum
      this.getTableList()
    },
    // 保存勾选人类活动
    selectActivities () {
      if (this.multipleActiveSelection.length < 1) {
        this.$Notice.error({ title: '人类活动不能为空！' })
      } else if (this.multipleActiveSelection.length > 1) {
        this.$Notice.error({ title: '只能选择一个人类活动！' })
      } else {
        this.actListId = this.multipleActiveSelection[0].id
        this.getMapTableList(this.multipleActiveSelection[0].id)
        this.handleCancel()
      }
    },
    // 勾选人类活动checkbox事件
    handleSelectionChange (val) {
      this.multipleActiveSelection = val
    },
    /* 取消选择人类活动 */
    handleCancel () {
      this.activitiesModal = false
      this.selectActiveRow = []
      this.multipleActiveSelection = []
    },
    /* 获取人类活动树形列表 */
    ActiveLists () {
      this.loading1 = true
      getActiveLists({
        data: {}
      }).then((res) => {
        if (res.data.code === '0000') {
          console.log(res.data.data)
          this.data1 = res.data.data
          this.loading1 = false
          res.data.data.forEach(item => {
            if (item.type === 1) {
              this.farmArr.push(item)
            }
            if (item.type === 2) {
              this.ConArr.push(item)
            }
            if (item.type === 3) {
              this.otherArr.push(item)
            }
          })
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 获取审核详情 */
    handleAudit () {
      this.loading1 = true
      getAudit({
        data: {
          id: String(this.activitiesValue)
        }
      }).then((res) => {
        if (res.data.code === '0000') {
          this.loading1 = false
        }
      }).catch(() => {
        this.$Notice.error({ title: '服务器错误' })
      })
    },
    /* 查看问题点页面退出 */
    signOut (addActive) {
      this.addActive = addActive
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #evaluation-wrapper{
    .query-box{
      height: 50px;
      line-height: 50px;
      border-bottom: 1px solid #d3d3d3;
      margin: 0 15px 15px;
      overflow: hidden;
      .search-title {
        float: left;
        font-size: 14px;
        &:before {
          content: '';
          display: inline-block;
          width: 18px;
          height: 18px;
          background: url("../../../assets/redLine/sprite.png") 0 -63px no-repeat;
          background-size: auto;
          vertical-align: middle;
          margin-right: 8px;
        }
      }
      .search-form {
        float: right;
      }
    }
    .content_box {
      padding: 0 15px 15px;
      width: 100%;
      height: calc(100% - 65px);
      #map {
        float: left;
        width: 40%;
        height: 100%;
        border: 1px solid #000;
      }
      .table-wrapper {
        float: right;
        width: calc(60% - 15px);
        height: 100%;
        p {
          margin-bottom: 5px;
          font-size: 16px;
          text-align: left;
        }
        &>div {
          margin-bottom: 15px;
          table {
            width: 100%;
            border: 1px solid #ccc;
            border-collapse: collapse;
          }
          thead th{
            text-align: center;
            background-color: #fafafa;
          }
          th,td {
            padding-left: 20px;
            border: 1px solid #e9eaec;
            line-height: 30px;
          }
        }
      }
      .page_box{
        width: 100%;
        padding: 15px 0 0;
        text-align: right
      }
    }
    .seeDetails {
      margin: 0 !important;
      height: calc(100% - 70px);
      width: calc(60% - 30px);
      position: absolute;
      right: -120%;
      top: 65px;
      z-index: 1356;
      background: #fff;
      transition: all 0.8s ease;
      .closeBtn{
        position: absolute;
        right:10px;
        bottom:10px;
        z-index: 131531313;
      }
    }
    .addActive{
      right: 15px;
    }
  }
</style>
